.header {
  display: flex;
  width: 100%;
  height: 70px;
  top: 0;
  left: 0;
  background: #FFFFFF 0 0 no-repeat padding-box;
  box-shadow: 0 3px 6px #00000029;
  opacity: 1;
}

.buttons {
  border: none;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
  background: #ffffff;
}

.title {
  top: 16px;
  left: 136px;
  width: 142px;
  height: 37px;
  text-align: left;
  color: #08A6E4;
  letter-spacing: 0;
}

.button1 {
  padding: 10px;
  background: #FFFFFF;
  border: none;
  color: #6c6969;
  font-size: 15px;
  font-weight: 600;
  top: 28px;
  left: 946px;
  width: 60px;
  height: 15px;
}

.button1:hover {
  border-bottom: 4px solid #08A6E4;
}

